function joinPaths(...paths: string[]): string {
  return paths.reduce((acc, curr) => {
    if (acc.endsWith("/")) {
      return acc + curr;
    } else {
      return acc + "/" + curr;
    }
  });
}

export function resource(url: string): string {
  return joinPaths(`${process.env.PUBLIC_URL}`, "assets", url);
}
