import React, { useMemo, useState } from "react";
import classNames from "classnames";
import { resource } from "utils";
import { Link, useLocation, useNavigate } from "react-router-dom";

const SUB_PATH = ["ABOUT", "ANONVERSE", "TEAM", "MANIFESTO", "ROADMAP"] as const;
type SubPath = typeof SUB_PATH[number];

export const Header: React.FC = () => {
  const [isOpen, setIsOpen] = useState(false);
  const { pathname } = useLocation();
  const navigate = useNavigate();

  const currentPath = useMemo<SubPath>(() => {
    const [, path0] = pathname.split("/");
    console.log(path0);
    return path0.toUpperCase() as SubPath;
  }, [pathname]);

  function switchIsOpen() {
    setIsOpen(!isOpen);
  }

  function changeSubPath(pathname: string) {
    setIsOpen(false);
    navigate("/" + pathname.toLocaleLowerCase());
  }

  return (
    <div className="h-full flex flex-col text-green-500 max-w-screen-2xl md:items-center md:justify-between md:flex-row">
      <div className="flex flex-row items-center justify-between">
        <Link to="/" className="focus:outline-none focus:shadow-outline">
          <img className="object-contain h-10" src={resource("/logo.svg")} alt="" />
        </Link>

        <button className="md:hidden rounded-lg focus:outline-none focus:shadow-outline" onClick={switchIsOpen}>
          <svg fill="currentColor" viewBox="0 0 20 20" className="w-6 h-6">
            {!isOpen && (
              <path
                fillRule="evenodd"
                d="M3 5a1 1 0 011-1h12a1 1 0 110 2H4a1 1 0 01-1-1zM3 10a1 1 0 011-1h12a1 1 0 110 2H4a1 1 0 01-1-1zM9 15a1 1 0 011-1h6a1 1 0 110 2h-6a1 1 0 01-1-1z"
                clipRule="evenodd"
              ></path>
            )}
            {isOpen && (
              <path
                fillRule="evenodd"
                d="M4.293 4.293a1 1 0 011.414 0L10 8.586l4.293-4.293a1 1 0 111.414 1.414L11.414 10l4.293 4.293a1 1 0 01-1.414 1.414L10 11.414l-4.293 4.293a1 1 0 01-1.414-1.414L8.586 10 4.293 5.707a1 1 0 010-1.414z"
                clipRule="evenodd"
              ></path>
            )}
          </svg>
        </button>
      </div>

      <nav
        className={classNames(
          "flex-col font-bold bg-opacity-60 bg-black md:bg-transparent flex-grow pb-4 md:pb-0 md:flex md:justify-end md:flex-row",
          {
            flex: isOpen,
            hidden: !isOpen,
          }
        )}
      >
        {SUB_PATH.map((pathname) => (
          <div
            key={pathname}
            className={classNames(
              "px-4 py-2 mt-2 md:mt-0 duration-300 hover:text-green-200 no-underline focus:outline-none focus:shadow-outline border-b md:border-none border-green-100 cursor-pointer",
              {
                "text-green-200": currentPath === pathname,
                "md:underline  underline-offset-4": currentPath === pathname,
              }
            )}
            onClick={() => changeSubPath(pathname)}
          >
            {pathname}
          </div>
        ))}
      </nav>
    </div>
  );
};
